<template>
    <div class="p-20">

        <div style="margin-bottom: 20px">
            <el-select v-model="valueType"
                       @change="handleTypeChange">
                <el-option
                        v-for="item in optionsType"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="valueStatus"
                       :disabled="valueStatusDisabled"
                       @change="handleStatusChange">
                <el-option
                        v-for="item in optionsStatus"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>

            <el-input style="margin-left: 10px;width: 300px" v-model="input"
                      placeholder="请输入手机、姓名、订单号"></el-input>

            <span class="demonstration" style="margin-left: 10px">创建日期</span>
            <el-date-picker
                    v-model="valueDate"
                    align="right"
                    :clearable="true"
                    type="date"
                    @change="handleChangeDate"
                    placeholder="选择日期">
            </el-date-picker>

            <span style="margin-left: 10px">收款账户</span>
            <el-select v-model="inputData.receiptBankAccount" clearable placeholder="全部账户">
                <el-option
                        v-for="item in optionsAccount"
                        :key="item.id"
                        :label="accountCode(item.bankName,item.bankAccount)"
                        :value="item.bankAccount">
                </el-option>
            </el-select>
            <el-button type="primary" style="margin-left: 30px"
                       @click.stop="query(pageNum = 1,inputData.transStatus)">查询
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间"
                    border
                    tooltip-effect="dark"
                    style="width: 100%;"
                    @selection-change="handleSelectionChange"
                    @current-change="handleTableCurrentChange">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
        </div>


        <div class="pagination">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageNum"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="inputData.page.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageTotal">
            </el-pagination>
        </div>

    </div>
</template>

<script>

    import fetch from '@/utils/fetch';

    import DCTP from '@/utils/dctp';

    const transStatusRechargeScope = [{value: 0, label: '全部状态'}, {value: 1, label: '未付款'},
        {value: 2, label: '待审核'}, {value: 3, label: '充值成功'},
        {value: 4, label: '用户撤销'}, {value: 5, label: '系统取消'}];

    const transStatusWithdrawScope = [{value: 0, label: '全部状态'}, {value: 2, label: '等待提现'},
        {value: 6, label: '正在处理'}, {value: 3, label: '提现成功'}, {value: 4, label: '用户撤消'},
        {value: 5, label: '系统撤消'}];

    export default {
        data() {
            return {
                //表格属性
                tableData: [],
                tableHeader: [
                    {
                        prop: "id",
                        label: "订单号",
                        width: 80
                    }, {
                        prop: "username",
                        label: "真实姓名",
                        width: 100
                    }, {
                        prop: "userPhone",
                        label: "手机号码",
                        width: 150
                    }, {
                        prop: "type",
                        label: "类型",
                        width: 80,
                        formatter(row, column, value) {
                            return value === 1 ? '充值' : (value === 2 || value === 3 ? '提现' : value);
                        }
                    }, {
                        prop: "amount",
                        label: "金额",
                        width: 120
                    }, {
                        prop: "fee",
                        label: "手续费",
                        width: 120
                    }, {
                        prop: "transStatus",
                        label: "状态",
                        width: 120,
                        formatter(row, column, value) {
                            if (row.type === 1) {
                                let item = DCTP.utils.findDataByValue(transStatusRechargeScope, value, 'value');
                                return item ? item.label : value;
                            }
                            if (row.type === 2 || row.type === 3) {
                                let item = DCTP.utils.findDataByValue(transStatusWithdrawScope, value, 'value');
                                return item ? item.label : value;
                            }
                        }
                    }, {
                        prop: "payee",
                        label: "付款人",
                        width: 80
                    }, {
                        prop: "payBankName",
                        label: "支付/提现账户",
                        width: 180
                    }, {
                        prop: "payBankAccount",
                        label: "支付/提现帐号",
                        width: 240
                    }, {
                        prop: "payBankAddress",
                        label: "开户行地址",
                        width: 120
                    }, {
                        prop: "receiptBankName",
                        label: "收款账户",
                        width: 120
                    }, {
                        prop: "receiptBankAccount",
                        label: "收款帐号",
                        width: 240
                    }, {
                        prop: "createTime",
                        label: "创建时间",
                        width: 180,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value);
                        }
                    }, {
                        prop: "updateTime",
                        label: "最后修改时间",
                        width: 180,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value);
                        }
                    }, {
                        prop: "auditUserId",
                        label: "审核人",
                        formatter(row, column, value) {
                            return value === 0 ? '' : value;
                        }
                    }],
                multipleSelection: [],
                currentRow: null,

                //下拉框属性  - 状态
                optionsStatus: [{value: 0, label: '全部状态'}],
                valueStatus: 0,
                valueStatusDisabled: true,
                // 下拉框  - 类型
                optionsType: [{value: 0, label: '全部类型'}, {value: 1, label: '人民币充值'}, {value: 2, label: '人民币提现'}],
                valueType: 0,
                // 下拉框  - 银行账户
                optionsAccount: [],
                valueAccount: '',
                // 输入框
                input: '',
                inputData: {
                    username: '',    // 用户名
                    type: 0,   // 交易类型
                    transStatus: 0,  // 交易状态
                    createTime: '',   // 创建时间
                    receiptBankName: '',
                    receiptBankAccount: '',
                    page: {
                        pageNum: '',   // 页数
                        pageSize: 10  // 每页数量
                    }
                },
                valueDate: '',
                // 其他属性
                pageNum: 1,           // 默认第一页
                pageTotal: null,      // 表格总条数
                listLoading: true
            }
        },

        methods: {
            handleSelectionChange(val) {
                this.multipleSelection = val;
                console.log(this.multipleSelection);
            },
            handleSizeChange(val) {
                this.inputData.page.pageSize = val;
                this.query(this.pageNum);
                console.log(`每页 ${val} 条`);
            },
            handleTableCurrentChange(val) {
                this.currentRow = val;
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.query(val);
            },
            handleStatusChange(val) {
                this.inputData.transStatus = val;
            },
            handleTypeChange(val) {
                switch (val) {
                    case 0:
                        this.valueStatusDisabled = true;
                        this.optionsStatus = [{value: 0, label: '全部状态'}];
                        break;
                    case 1:
                        this.valueStatusDisabled = false;
                        this.optionsStatus = transStatusRechargeScope;
                        break;
                    case 2:
                        this.valueStatusDisabled = false;
                        this.optionsStatus = transStatusWithdrawScope;
                        break;
                }
                this.valueStatus = 0;
                this.inputData.type = val;
            },
            handleChangeDate(time) {
                this.inputData.createTime = time;
            },
            // 弹出一个确认窗口
            showConfirmDialog(act) {
                let _this = this;
                if (_this.multipleSelection.length === 0) {
                    this.getMessage("请选择信息", false);
                    return;
                }
                this.showDialog(act);
            },
            // 弹出对话框
            showDialog(act) {
                let _this = this;
                switch (act) {
                    case 'examine':
                        this.$confirm('已选' + _this.multipleSelection.length + '条信息，确认审核？', '提示', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                        }).then(() => {
                            this.examine();
                        }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: '操作已取消'
                            });
                        });
                        break;
                    case 'cancel':
                        this.$confirm('共选中' + _this.multipleSelection.length + '条信息，确认取消？', '提示', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                        }).then(() => {
                            this.cancel();
                        }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: '操作已取消'
                            });
                        });
                        break;
                    case 'cancelAll':
                        this.$confirm('取消全部充值订单？', '提示', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                        }).then(() => {
                            this.cancelAll();
                        }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: '操作已取消'
                            });
                        });
                        break;
                }
            },
            // 查询
            query(pageNum) {
                let _this = this;
                this.listLoading = true;
                _this.inputData.username = _this.input;
                _this.inputData.page.pageNum = pageNum;
                console.log(_this.inputData.receiptBankAccount);
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/rmbtransactions/query',
                        method: 'post',
                        data: _this.inputData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.pageTotal = response.data.page.count;
                            _this.listLoading = false;
                        }
                    })
                })
            },
            // 审核
            examine() {
                let _this = this;
                let currentRows = [];
                for (let i = 0; i < this.multipleSelection.length; i++) {
                    currentRows.push(this.multipleSelection[i].id);
                }
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/rmbrecharge/check',
                        method: 'put',
                        data: {ids: currentRows}
                    }).then(function (response) {
                        if (response.result) {

                        }
                        _this.getMessage(response.msg, response.result);
                        _this.query();
                    })
                })
            },
            getMessage(msg, seccess) {
                if (seccess) {
                    this.$message({
                        showClose: true,
                        message: msg,
                        type: 'success'
                    })
                } else {
                    this.$message({
                        showClose: true,
                        message: msg,
                        type: 'error'
                    })
                }
            },
            // 取消充值
            cancel() {
                let _this = this;
                let currentRows = [];
                for (let i = 0; i < this.multipleSelection.length; i++) {
                    currentRows.push(this.multipleSelection[i].id);
                }
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/rmbrecharge/cancel',
                        method: 'put',
                        data: {ids: currentRows}
                    }).then(function (response) {
                        if (response.result) {

                        }
                        _this.getMessage(response.msg, response.result);
                        _this.query();
                    })
                })
            },
            // 全部取消
            cancelAll() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/rmbrecharge/cancelall',
                        method: 'put'
                    }).then(function (response) {
                        if (response.result) {

                        }
                        _this.getMessage(response.msg, response.result);
                        _this.query();
                    })
                })
            },
            // 获取银行账户列表
            getAccount() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/receiptaccounts?pageNum=1&pageSize=100',
                        method: 'get'
                    }).then(function (response) {
                        console.log(response);
                        if (response.result) {
                            _this.optionsAccount = response.data.data;
                        }
                    })
                })
            },
            // 账户打码   账户名 + **账户号后四位
            accountCode(name, number) {
                if (name !== null && number !== null) {
                    return name + '  **' + number.substring(number.length - 4, number.length);
                }
            }
        },
        created() {
            this.query(this.pageNum);
            this.getAccount();
        }
    }
</script>


